import * as echarts from 'echarts';
import axios from 'axios'
import { Table } from 'antd'
import React, {useState,useEffect} from "react";
import '../router-view.css'

export default function Demo(){
   

  
   const [arr,setarr] = useState([])
   //
   axios.post('http://crm.cimns.com/index.php/bi/customer/userCycle').then((data)=>{
      if(arr.length<=0){
      let a = [...arr]
      a = [...data.data.data.users]
      setarr(a)
      }
   },(err)=>{
   console.log(err)
   })

    // 
    useEffect (() => {
      
      var myChart = echarts.init(document.getElementById('main'));
      myChart.setOption({
       tooltip: {
          trigger: 'axis',
          axisPointer: {            // 
              type: 'shadow'        // 'line' | 'shadow'
          }
      },
       color: ['#6ca2ff','#ff7474'],
       legend: {
         bottom:0
       },
      
    
       dataset: {
           source: [
               ['product', '', ''],
               ['2021-01', 0,0],
               ['2021-02', 0,0],
               ['2021-03', 0,0],
               ['2021-04', 0,0],
               ['2021-05', 0,0],
               ['2021-06', 0,0],
               ['2021-07', 0,0],
               ['2021-08', 0,0],
               ['2021-09',0,0],
               ['2021-10', 0,0],
               ['2021-11', 0,0],
               ['2021-12', 0,0]
           ]
       },
       xAxis: {type: 'category'},
       yAxis: [
         {
            type: 'value',
            name: '',
            min: 0,
            max: 1,
            interval: 0.2,
            axisLabel: {
                formatter: '{value} '
            }
        },
        {
            type: 'value',
            name: '',
            min: 0,
            max: 1,
            interval: 0.2,
            axisLabel: {
                formatter: '{value} '
            }
        },
           {
           type : 'value',
           axisLine:{show:true,
           lineStyle:{color:'#ccc'}
           },
           axisTick:{
           show:false,              
           },
           axisLabel:{
           
           },
           splitNumber:5
           
                       }
          ],
       
       series: [
           {type: 'bar'},
           {type: 'bar'},
       ]
    })
      })
      //

     
         return(
            <div className="border_boxa">
              
      <div className="max_box">
      <div className="padin">
         <div className="max_select">
            <div className="item_select">
               <select key="a"  >
               <option key="volvo" ></option>
               <option key="saab" ></option>
               <option key="opel" ></option>
               <option key="audi" ></option>
               <option key="opel1" ></option>
               <option key="audi1" ></option>
               <option key="opel3"  selected></option>
               <option key="audi0" ></option>
               <option key="opel2" ></option>
               </select>
            </div>
            <div className="item_select">
               <select key="b"  >
               <option key ="volvo" selected></option>
               <option key ="saab"></option>
               </select>
            </div>
            <div className="item_select">
               <select key="c" >
               <option key="" disabled selected hidden></option>
               {arr.map((item,index)=>{
                  return(
                     <option key ={index}>{item.realname}</option>
                  )
               })}
               
               
               </select>
            </div>
               <button className="btn1"></button>
            <div>
               <p style={{color:'#333',fontSize:'16px',marginTop:'17px',marginLeft:'101px'}}></p>
            </div>
         </div>
         <div id="main"></div>
         <div className="tabl">

         <div className="no3_1_1_box_content_list">
                    <div className="no3_1_1_box_content_list_top">
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>
                    <div className="no3_1_1_box_content_list_ciontent">
                        {arr.map((item, index) => {
                            return (
                                <div className="no3_1_1_box_content_list_ciontent_item" key={index}>
                                    <div>{item.realname}</div>
                                    <div>{item.cycle}</div>
                                    <div>{item.customer_num}</div>
                                </div>
                            )
                        })}
                    </div>
                </div>
         </div>
         </div>
         
      </div>
   </div>
         )
}
