import * as echarts from 'echarts';
import axios from 'axios'
import { Table } from 'antd'
import React, {useState} from "react";
import Zhe1 from './s-04-router/zhexian'
import Zhu1 from './s-04-router/zhuzhuang'
import Bing1 from './s-04-router/bingzhuang'
import './router-view.css'


let A =function(){
   const [arr,setarr] = useState([])
    //
    axios.post('http://crm.cimns.com/index.php/admin/structures/getSubUserByStructrue').then((data)=>{
      if(arr.length<=0){
         let a = [...arr]
         a = [...data.data.data]
         setarr(a)
         // console.log(a);
       }
    },(err)=>{
      console.log(err)
    })
    
//   
const [val,setval] = useState('')
function Qh(event){
 
   setval(event.target.value)
}
    
    let arr1=[]
   return(
      <div className="border_box">
              
      <div className="max_box">
      <div className="padin">
         <div className="max_select">
            <div className="item_select">
               <select key="a" >
                  <option key ="" ></option>
                  <option key =""></option>
                  <option key=""></option>
                  <option key=""></option>
                  <option key=""></option>
                  <option key=""></option>
                  <option key="" selected></option>
                  <option key=""></option>
                  <option key=""></option>
               </select>
            </div>
            <div className="item_select">
               <select key="b" >
               <option key ="" selected></option>
               <option key =""></option>
               </select>
            </div>
            <div className="item_select">
               <select key="c" >
                  
               <option value="a"></option>
               {arr.map((item,index)=>{
                  return(
                     <option key={index} value ={index}>{item.realname}</option>
                  )
               })}
               </select>
            </div>
            <div className="item_select" >
               <select key="d"  value={val} onChange={Qh} >
                  <option key ="1" style={{display:'none'}}></option>
                  <option key ="" selected></option>
                  <option key ="" ></option>
                  <option key ="" ></option>
               </select>
            </div>
               <button className="btn1"></button>
            
         </div>
         <div className="mains">
               <div className="tab_1" style={{opacity:val===''?"1":'0',zIndex:val===''?"99":'1'}}>
                  <Zhe1></Zhe1>
               </div>
               <div className="tab_2" style={{opacity:val===''?"1":'0',zIndex:val===''?"99":'1'}}>
                  <Bing1></Bing1>
               </div>
               <div className="tab_3" style={{opacity:val===''?"1":'0',zIndex:val===''?"99":'1'}}>
                   <Zhu1></Zhu1>
               </div>
         </div>
         <div className="tabl">

         <div className="no3_1_1_box_content_list ">
                    <div className="no3_1_1_box_content_list_top">
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>
                    <div className="no3_1_1_box_content_list_ciontent ">
                        {arr1.map((item, index) => {
                            return (
                                <div className="no3_1_1_box_content_list_ciontent_item" key={index}>
                                    <div>{item.realname}</div>
                                    <div>{item.customer_num}</div>
                                    <div>{item.deal_customer_num}</div>
                                    <div>{item.deal_receivables_rate}</div>
                                    <div>{item.contract_money}</div>
                                    <div>{item.receivables_money}</div>
                                    <div>{item.un_receivables_money}</div>
                                    <div>{item.un_receivables_money}</div>
                                </div>
                            )
                        })}
                    </div>
                </div>
         </div>
         <div className="ppp">
            <p>50</p>
         </div>
         </div>
         
      </div>
   </div>
   )
}

export default A


