import { HashRouter} from "react-router-dom"
import './man2_6.css'
import { Select,Table,Pagination} from 'antd';

const provinceOptio = ['', ''];
const handleProvinceChange = value => {};
const { Option } = Select;
const columns = [
    { title: '', dataIndex: 'address', key: '1' },
    { title: '', dataIndex: 'address', key: '2' },
    { title: '', dataIndex: 'address', key: '3' },
    { title: '', dataIndex: 'address', key: '4' },
    { title: '', dataIndex: 'address', key: '5' },
    { title: '', dataIndex: 'address', key: '6' },
    { title: '', dataIndex: 'address', key: '7' },
    { title: '', dataIndex: 'address', key: '8' },
    { title: '', dataIndex: 'address', key: '9' },
   
  ];

let Man2_6 = function (params){
    return(
        <HashRouter>
            <div className="hktx">
                <div className="hktx_title">
                    
                </div>
                <div className="hktx_bar">
                    <div className="hktx_data">
                        <Select  style={{width:"185px"}} defaultValue={provinceOptio[0]} onChange={handleProvinceChange}>
                            {provinceOptio.map(province => (
                                <Option key={province}>{province}</Option>
                             ))}
                        </Select>
                    </div>
                </div>
                <div className="lxkh_table">
                    <Table columns={columns} scroll={{ x: 1106 }} />
                </div>
                <div className="lxkh_footer">
                    <Pagination size="small" total={50} showSizeChanger showQuickJumper />
                </div>
            </div>
        </HashRouter>
    )
}
export default Man2_6