import { HashRouter} from "react-router-dom"
import './man2_4.css'
import { Select, Button,Modal,Space,message,Table,Pagination} from 'antd';
import React,{useState } from 'react'
import {
    CloseCircleOutlined,
    ExclamationCircleOutlined,
  } from '@ant-design/icons';

const provinceOptio = ['', ''];
const handleProvinceChange = value => {};
const { Option } = Select;
const provinceDa = ['','','','','','','','','','','','','','','',''];
const provinceD = ['','','','','','','','','','','',''];
const provinceDaa = ['','IT///','','','','','','/','','','',''];
const { confirm } = Modal;
const columns = [
    {title: '',dataIndex: 'address',key: '1',width: 100, },
    {title: '',dataIndex: 'address',key: '2',width: 100,},
    {title: '',dataIndex: 'address',key: '3',width: 100,},
    {title: '',dataIndex: 'address',key: '4',width: 100,},
    {title: '',dataIndex: 'address',key: '5',width: 100,},
    {title: '',dataIndex: 'address',key: '6',width: 100,},
    {title: '',dataIndex: 'address',key: '7',width: 150,},
    {title: '', dataIndex: 'address', key: '8',width: 100, },
    {title: '',dataIndex: 'address',key: '9',width: 100,},
    {title: '', dataIndex: 'address', key: '10',width: 100, },
    {title: '',dataIndex: 'address',key: '11',width: 100,},
    {title: '', dataIndex: 'address', key: '12' ,width: 100,},
    {title: '',dataIndex: 'address',key: '13',width: 100,},
    {title: '', dataIndex: 'address', key: '14', width: 100,},
    {title: '',dataIndex: 'address',key: '15',width: 100,},
    {title: '', dataIndex: 'address', key: '16',width: 100, },
    {title: '', dataIndex: 'address', key: '17' ,width: 100,},
    {title: '',key: 'operation',fixed: 'right',width: 100,render: () => <a>action</a>,}, 
    
  ];
  const data = [];

function showConfirm() {
    confirm({
      title: '',
      icon: <ExclamationCircleOutlined />,
      content: '',
      onOk() {
        console.log('OK');
        message.success('')
      },
      onCancel() {
        console.log('Cancel');
        message.warning('');
      },
      okText:'',
      cancelText:'',

    });
  }
  function showTotal(total) {
    return `Total ${total} items`;
  }
let Man2_4 = function (params){
    const [visible, setVisible] = useState(false);
    return(
        <HashRouter>
             <div className="shht">
                <div className="shht_title">
                    
                </div>
                <div className="shht_bar">
                    <div className="shht_data">
                        <Select  style={{width:"185px"}} defaultValue={provinceOptio[0]} onChange={handleProvinceChange}>
                            {provinceOptio.map(province => (
                                <Option key={province}>{province}</Option>
                             ))}
                        </Select>
                        <Button type="primary" onClick={() => setVisible(true)} style={{background:"none",border:"none",color:"#000",boxShadow:"0 0 0 0"}}>
                                <img src="data:image/png;base64," style={{width:"12px",height:"13px",margin: "0 5px",verticalAlign:"middle"}}/>
                                
                        </Button>
                        <Modal
                                title=""
                                centered
                                visible={visible}
                                onOk={() => setVisible(false)}
                                onCancel={() => setVisible(false)}
                                width={900}
                                okText=''
                                cancelText=''
                            >
                               <div style={{marginBottom:"10px"}}></div>
                               <Select  style={{width:"286px",height:"40px",float:"left"}} defaultValue={provinceDa[0]} onChange={handleProvinceChange}>
                                    {provinceDa.map(province => (
                                        <Option key={province}>{province}</Option>
                                    ))}
                                </Select>
                               <div style={{width:"35px",float:"left"}}>&nbsp;</div>
                               <Select  style={{width:"143px",height:"40px",marginRight:"35px"}} defaultValue={provinceD[0]} onChange={handleProvinceChange}>
                                    {provinceD.map(province => (
                                        <Option key={province}>{province}</Option>
                                    ))}
                                </Select>
                                <Select  style={{width:"286px",height:"40px"}} defaultValue={provinceDaa[0]} onChange={handleProvinceChange}>
                                    {provinceDaa.map(province => (
                                        <Option key={province}>{province}</Option>
                                    ))}
                                </Select>
                                <Space>
                                    <Button onClick={showConfirm} style={{border:"none"}}>
                                        <CloseCircleOutlined />
                                    </Button>
                                </Space>
                                <p>+</p>   
                            </Modal>
                    </div>
                </div>
                <div className="shht_table">
                    <Table columns={columns} dataSource={data} scroll={{ x: 1750}} />
                </div>
                <div className="wdkh_footer">
                    <Pagination size="small" total={50} showSizeChanger showQuickJumper />
                </div>
            </div>
        </HashRouter>
    )
}
export default Man2_4