import * as echarts from 'echarts';
import axios from 'axios'
import { Table } from 'antd'
import React, {useState,useEffect} from "react";
import './router-view.css'


let A =function(){
   const [arr,setarr] = useState([])

   //
   axios.post('http://crm.cimns.com/index.php/bi/customer/statistics').then((data)=>{
       if(arr.length<=0){
         let a = [...arr]
         a = [...data.data.data]
         setarr(a)
       }
    },(err)=>{
      console.log(err)
    })

       //
      let arrl=['2021-01','2021-02','2021-03','2021-04','2021-05','2021-06','2021-07','2021-08','2021-09','2021-10','2021-11','2021-12']

   return(
      <div className="border_box">
              
      <div className="max_box">
      <div className="padin">
         <div className="max_select">
            <div className="item_select">
               <select key="a"  >
               <option key ="volvo" ></option>
                        <option key ="saab"></option>
                        <option key="opel"></option>
                        <option key="audi"></option>
                        <option key="opel1"></option>
                        <option key="audi1"></option>
                        <option key="opel2" selected></option>
                        <option key="audi2"></option>
                        <option key="opel3"></option>
               </select>
            </div>
            <div className="item_select">
               <select key="b" >
               <option key ="volvo" selected></option>
               <option key ="saab"></option>
               </select>
            </div>
            <div className="item_select">
               <select key="c"  >
               <option key="" disabled selected hidden></option>
               {arr.map((item,index)=>{
                  return(
                     <option key ={index}>{item.realname}</option>
                  )
               })}
               
               
               </select>
            </div>
               <button className="btn1"></button>
              
         </div>
         <div className="tabl t1">
         <p style={{color:'#333',fontSize:'16px',marginTop:'17px',marginLeft:'101px'}}>0000</p>
         <div className="no3_1_1_box_content_list">
                    <div className="no3_1_1_box_content_list_top">
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>
                    <div className="no3_1_1_box_content_list_ciontent">
                        {arrl.map((item, index) => {
                            return (
                                <div className="no3_1_1_box_content_list_ciontent_item" key={index}>
                                    <div>{item}</div>
                                    <div>0</div>
                                    <div>0</div>
                                    <div>0</div>
                                </div>
                            )
                        })}
                    </div>
                </div>

         </div>
         </div>
         
      </div>
   </div>
   )
}

export default A