import * as echarts from 'echarts';
import axios from 'axios'
import React, {useState,useEffect} from "react";
import './router-view.css'


let A =function(){
   const [arr,setarr] = useState([])

   //
   axios.post('http://crm.cimns.com/index.php/bi/customer/statistics').then((data)=>{
       if(arr.length<=0){
         let a = [...arr]
         a = [...data.data.data]
         setarr(a)
       }
    },(err)=>{
      console.log(err)
    })

       //
    useEffect(()=>{
              //   outerHTMLtableHTML<table>HTMLcharseturf-8
        var html1 = "<html><head><meta charset='utf-8' /></head><body>" + document.querySelector(".bg_max").outerHTML + "</body></html>";
        // Blob
        var blob = new Blob([html1], { type: "application/vnd.ms-excel" });
        var a = document.querySelector(".btn_box");
        // URL.createObjectURL()ablob URL
        a.href = URL.createObjectURL(blob);
        // 
        a.download = ".xls";
    })
   return(
      <div className="border_box">
              
      <div className="max_box">
      <div className="padin">
         <div className="max_select">
            <div className="item_select">
               <select key="a"  >
               <option key ="" ></option>
                  <option key =""></option>
                  <option key=""></option>
                  <option key=""></option>
                  <option key=""></option>
                  <option key=""></option>
                  <option key="" selected></option>
                  <option key=""></option>
                  <option key=""></option>
               </select>
            </div>
            <div className="item_select">
               <select key="b" >
               <option key ="volvo" selected></option>
               <option key ="saab"></option>
               </select>
            </div>
            <div className="item_select">
               <select key="c"  >
               <option key="" disabled selected hidden></option>
               {arr.map((item,index)=>{
                  return(
                     <option key ={index}>{item.realname}</option>
                  )
               })}
               
               
               </select>
            </div>
               <button className="btn1"></button>
              
         </div>
         <div className="tabl t1">
            <div className="a_box">
            <a  className="btn_box">
               
            </a>
            </div>
         
         <div className="no3_1_1_box_content_list bg_max">
                    <div className="no3_1_1_box_content_list_top">
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>
                    <div className="no3_1_1_box_content_list_ciontent bg_box">
                        {arr.map((item, index) => {
                            return (
                                <div className="no3_1_1_box_content_list_ciontent_item" key={index}>
                                    <div>{item.realname}</div>
                                    <div>{item.customer_num}</div>
                                    <div>{item.deal_customer_num}</div>
                                    <div>{item.deal_receivables_rate}</div>
                                    <div>{item.contract_money}</div>
                                </div>
                            )
                        })}
                    </div>
                </div>

         </div>
         </div>
         
      </div>
   </div>
   )
}

export default A