import { HashRouter } from "react-router-dom"
import './man3.css'
import { Input, Space, Menu, Dropdown, Button, message, Select, Modal, DatePicker, Table, Pagination, Transfer  } from 'antd';
import { AudioOutlined, DownOutlined } from '@ant-design/icons';
import React, { useState, useEffect } from 'react';
import {
  CloseCircleOutlined,
  ExclamationCircleOutlined,
  SettingOutlined
} from '@ant-design/icons';
import Man3trans from './man3-trans'
const { Search } = Input;
/////

const menu = (
  <Menu onClick={handleMenuClick}>
    <Menu.Item key="1">
      
      </Menu.Item>
    <Menu.Item key="2">
      
      </Menu.Item>
  </Menu>
);
const menuu = (
  <Menu onClick={handleMenuClick}>
    <Menu.Item key="1">
      
    </Menu.Item>
    <Menu.Item key="2">
      
    </Menu.Item>

    <Menu.Item key="3">
      
    </Menu.Item>
    <Menu.Item key="4">
      
    </Menu.Item>
    <Menu.Item key="5">
      +
    </Menu.Item>
    <Menu.Item key="6">
      +
    </Menu.Item>
  </Menu>
);
const provinceDa = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''];
const provinceD = ['', '', '', '', '', '', '', '', '', '', '', ''];
const provinceDaa = ['', 'IT///', '', '', '', '', '', '/', '', '', '', ''];
const provinceDbb = ['', '', '', '', '', '', '', '', '', '', '', '', '', ''];
const provinceDcc = ['IT///', '', ' ', '', ' ', '', '/', ' ', '', '', ' ']
const provinceDdd = ['A', 'B', ' C']
const handleProvinceChange = value => { };
const { Option } = Select;
function handleButtonClick(e) {
  message.info('Click on left button.');
  // console.log('click left button', e);
}

function handleMenuClick(e) {
  message.info('Click on menu item.');
  // console.log('click', e);
}
//////
const suffix = (
  <AudioOutlined
    style={{
      fontSize: 16,
      color: '#1890ff',
    }}
  />
);
const onSearch = value => console.log(value);
const { TextArea } = Input;
function onChange(date, dateString) {
  console.log(date, dateString);
}
const { confirm } = Modal;
function showConfirm() {
  confirm({
    title: '',
    icon: <ExclamationCircleOutlined />,
    content: '',
    onOk() {
      console.log('OK');
      message.success('')
    },
    onCancel() {
      console.log('Cancel');
      message.warning('');
    },
    okText: '',
    cancelText: '',

  });
}
function showTotal(total) {
  return `Total ${total} items`;
}

let Man3 = function (params) {
  const [visible, setVisible] = useState(false);
  const [visiblea, setVisiblea] = useState(false);
  const columns = [
    {
      title: <div>
        <div style={{width:"14px",height:"14px",float:"left",border:"1px #ddd solid",margin:"4px 9px 0 0"}}></div>
        
      </div>,
      dataIndex: 'age',
      defaultSortOrder: 'descend',
      sorter: (a, b) => a.age - b.age,
      fixed: 'left',
      width: 100,
    },
    {
      title: '',
      defaultSortOrder: 'descend',
      sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '1',
      width: 100,
    },
    {
      title: '',
      defaultSortOrder: 'descend',
      sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '2',
      width: 100,
    },
    {
      title: '', faultSortOrder: 'descend', width: 100,
      sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '3'
    },

    {
      title: '', faultSortOrder: 'descend', width: 100,
      sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '4'
    },

    {
      title: '', faultSortOrder: 'descend', width: 100,
      sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '5'
    },

    {
      title: '', faultSortOrder: 'descend', width: 120,
      sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '6'
    },
    {
      title: '', faultSortOrder: 'descend', width: 100,
      sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '7'
    },
    {
      title: '', faultSortOrder: 'descend', width: 100,
      sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '8'
    },
    {
      title: '', faultSortOrder: 'descend', width: 100,
      sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '9'
    },
    {
      title: '', faultSortOrder: 'descend', width: 100,
      sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '10'
    },
    {
      title: '', faultSortOrder: 'descend', width: 100,
      sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '11'
    },
    {
      title: '', faultSortOrder: 'descend', width: 100,
      sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '12'
    },
    { title: '', faultSortOrder: 'descend', width: 100, dataIndex: 'address', key: '13' },
    {
      title: <div>
       {/* <Man3trans></Man3trans> */}
        <SettingOutlined style={{marginLeft:"13px",marginTop:"14px"}}/>
      </div>,
      key: 'operation',
      fixed: 'right',
      width: 90,
      render: () => <a>action</a>,
    },
  ]
  return (
    <HashRouter>
      <div>
        <div className="man3_container">
          <div className="man3_title"></div>
          <div className="man3_input">
            <Search placeholder="//" onSearch={onSearch} style={{ width: 300 }} />
          </div>
          <div className="man3_dropdown">
            <div className="dropdown_left">
              <Button type="primary" onClick={() => setVisible(true)}>
                
              </Button>
              <Modal
                title=""
                centered
                visible={visible}
                onOk={() => setVisible(false)}
                onCancel={() => setVisible(false)}
                width={700}
                okText=''
                cancelText=''
              >
                <div className="section_header">
                  <div className="section_mark"></div>
                  <div className="section_title"></div>
                </div>
                <div className="section_content">
                  <form className="content_box">
                    <div className="box_left">
                      <div style={{ color: "red", float: "left" }}>*</div>
                      <div style={{ display: "inline-block", fontSize: "12px", margin: "5px 0", paddingLeft: "3px" }}></div>
                      <div>
                        <Input style={{ width: "280px", height: "34px" }} />
                      </div>
                    </div>
                    <div className="box_right">
                      <div style={{ display: "inline-block", fontSize: "12px", margin: "5px 0", paddingLeft: "3px" }}></div>
                      <div>
                        <Select style={{ width: "280px", height: "40px" }} placeholder="" onChange={handleProvinceChange}>
                          {provinceDbb.map(province => (
                            <Option key={province}>{province}</Option>
                          ))}
                        </Select>
                      </div>
                    </div>
                    <div className="box_left">
                      <div style={{ display: "inline-block", fontSize: "12px", margin: "5px 0", paddingLeft: "3px" }}></div>
                      <div>
                        <Input style={{ width: "280px", height: "34px" }} />
                      </div>
                    </div>
                    <div className="box_right">
                      <div style={{ display: "inline-block", fontSize: "12px", margin: "5px 0", paddingLeft: "3px" }}></div>
                      <div>
                        <Input style={{ width: "280px", height: "34px" }} />
                      </div>
                    </div>
                    <div className="box_left">
                      <div style={{ display: "inline-block", fontSize: "12px", margin: "5px 0", paddingLeft: "3px" }}></div>
                      <div>
                        <Select style={{ width: "280px", height: "40px" }} placeholder="" onChange={handleProvinceChange}>
                          {provinceDcc.map(province => (
                            <Option key={province}>{province}</Option>
                          ))}
                        </Select>
                      </div>
                    </div>
                    <div className="box_right">
                      <div style={{ display: "inline-block", fontSize: "12px", margin: "5px 0", paddingLeft: "3px" }}></div>
                      <div>
                        <Select style={{ width: "280px", height: "40px" }} placeholder="" onChange={handleProvinceChange}>
                          {provinceDdd.map(province => (
                            <Option key={province}>{province}</Option>
                          ))}
                        </Select>
                      </div>
                    </div>
                    <div className="box_left">
                      <div style={{ display: "inline-block", fontSize: "12px", margin: "5px 0", paddingLeft: "3px" }}></div>
                      <div>
                        <Input style={{ width: "280px", height: "34px" }} />
                      </div>
                    </div>
                    <div className="box_right">
                      <div style={{ display: "inline-block", fontSize: "12px", margin: "5px 0", paddingLeft: "3px" }}></div>
                      <div>
                        <Space direction="vertical">
                          <DatePicker onChange={onChange} style={{ width: "280px", height: "34px" }} />
                        </Space>
                      </div>
                    </div>
                    <div className="box_left">
                      <div style={{ display: "inline-block", fontSize: "12px", margin: "5px 0", paddingLeft: "3px" }}></div>
                      <div>
                        <TextArea rows={4} style={{ width: "280px", height: "70px" }} />
                      </div>
                    </div>
                  </form>
                </div>
              </Modal>
            </div>
            <Space wrap style={{ padding: "0 12px" }}>
              <Dropdown overlay={menu}>
                <Button>
                   <DownOutlined />
                </Button>
              </Dropdown>
            </Space>
          </div>
        </div>
        <div className="man3_content">
          <div className="content_title">
            <div style={{ float: "left", paddingTop: "6px" }}></div>
            <div style={{ float: "left" }}>
              <Space wrap>
                <Dropdown overlay={menuu}>
                  <Button style={{ border: "none" }}>
                     <DownOutlined />
                  </Button>
                </Dropdown>
              </Space>
            </div>
            <div>
              <Button type="primary" onClick={() => setVisiblea(true)} style={{ background: "none", border: "none", color: "#000", boxShadow: "0 0 0 0", fontSize: "14px" }}>
                <img src="data:image/png;base64," style={{ width: "12px", height: "13px", margin: "0 5px", verticalAlign: "middle" }} />
                    
                </Button>
              <Modal
                title=""
                centered
                visible={visiblea}
                onOk={() => setVisiblea(false)}
                onCancel={() => setVisiblea(false)}
                width={900}
                okText=''
                cancelText=''
              >
                <div style={{ marginBottom: "10px" }}></div>
                <Select style={{ width: "286px", height: "40px", float: "left" }} defaultValue={provinceDa[0]} onChange={handleProvinceChange}>
                  {provinceDa.map(province => (
                    <Option key={province}>{province}</Option>
                  ))}
                </Select>
                <div style={{ width: "35px", float: "left" }}>&nbsp;</div>
                <Select style={{ width: "143px", height: "40px", marginRight: "35px" }} defaultValue={provinceD[0]} onChange={handleProvinceChange}>
                  {provinceD.map(province => (
                    <Option key={province}>{province}</Option>
                  ))}
                </Select>
                <Select style={{ width: "286px", height: "40px" }} defaultValue={provinceDaa[0]} onChange={handleProvinceChange}>
                  {provinceDaa.map(province => (
                    <Option key={province}>{province}</Option>
                  ))}
                </Select>
                <Space>
                  <Button onClick={showConfirm} style={{ border: "none" }}>
                    <CloseCircleOutlined />
                  </Button>
                </Space>
                <p>+</p>
                <div style={{ marginTop: "10px" }}>
                  <div style={{ width: "14px", height: "14px", border: "1px #ddd solid", float: "left", borderRadius: "2px", marginTop: "4px" }}></div>
                  <div style={{ fontSize: "13px", lineHeight: "19px", paddingLeft: "8px", display: "inline-block" }}></div>
                </div>
              </Modal>
            </div>
          </div>
          <div className="content_data">
            <Table columns={columns} scroll={{ x: 2000 }} />
          </div>
          <div className="man3_footer">
            <Pagination size="small" total={50} showSizeChanger showQuickJumper />
          </div>
        </div>
      </div>
    </HashRouter>
  )
}
export default Man3